.modal {
  width: 430px;
  display: none;
  position: fixed;
  top: 50%;
  margin: 0 auto;
  transform: translateY(-50%);
  right: 0;
  left: 0;
  z-index: 200;

  @media(max-width: $tablet) {
    width: 442px;
  }

  @media(max-width: $mobile) {
    width: 270px;
  }
}
.modal--open {
  display: block;
}
